





















































/* TODO: Change horizontal slide to (less intrusive) vertical */
#navigation[data-v-770bc34d] {
    width: 100%;
    overflow: hidden;
    /* background-color: #fafafa; */
}
#navigation:hover .title-wrapper[data-v-770bc34d] {
    cursor: crosshair;
    transition: .6s ease-in-out;
}
#navigation:hover .title[data-v-770bc34d] {
    left: -65%;
    transition: .6s ease-in-out;
}
#navigation:hover .nav-wrapper[data-v-770bc34d] {
    transition: .6s ease-in-out;
    left: 0%;
    opacity: 1.0;
    cursor: crosshair;
}
.title-wrapper[data-v-770bc34d] {
    position: absolute;
    width: 100%;
    text-align: center;
    transition: 2s ease-in-out;
}
.title[data-v-770bc34d] {
    position: relative;
    left: 0%;
    font-variant: small-caps;
    padding: 20px 0px 20px 0px;
    letter-spacing: 3px;
    transition: 2s ease-in-out;
}
.nav-wrapper[data-v-770bc34d] {
    position: relative;
    left: 65%;
    opacity: 0.0;
    transition: 2s ease-in-out;
}
.nav-item[data-v-770bc34d] {
    letter-spacing: 3px;
    display: inline-block;
    text-align: center;
    width: 24%;
    padding: 20px 0px 20px 0px;
    font-variant: small-caps;
    display: inline-block;
    transition: background-color .3s;
}
.nav-item[data-v-770bc34d]:hover {
    background-color: #fafafa; /* #f0f0f0; */
}
.active[data-v-770bc34d] {
    color: #777777;
}



